.main {
  display: flex;
  flex: 1 1 auto;
  width: calc(100% - 264px);

  &__content {
    flex: 1 1 auto;
    width: calc(100% - 300px);
    padding-right: calc(var(--gap) * 3);

    @include desctop() {
      max-width: 680px;
    }

    @include ipad() {
      padding-right: 0;
    }
  }

  &__aside {
    flex: 0 0 auto;
    width: 300px;

    @include desctop() {
      position: fixed;
      top: 56px;
      transform: translateX(100%);
      transition: transform 0.6s, opacity 0.6s;
      height: calc(100vh - 56px);
      width: 348px;
      right: 0;
      overflow-y: auto;
      background-color: var(--bg-color);
      padding: var(--gap) calc(var(--gap) * 0.5);
      opacity: 0;
      z-index: 10;
    }

    @include ipad() {
      width: 100vw;
    }
  }
}

.open-aside-menu {
  .main {
    &__aside {
      @include desctop() {
        width: 348px;
        opacity: 1;
        transform: translateX(0%);
        box-shadow: var(--box-shadow);
      }

      @include phone() {
        width: 100%;
      }
    }
  }
}
